body{
    width: 80%;   
    margin: auto;
}


.bk-to-shop_sh-cart{
    display: flex;
    justify-content: space-between;
}
.back-to-shopping{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.arrw-icon{
    width:1rem;
    margin-right: 10px;

}
.sh-cart{
    width:1.5rem;
}

.cart-items-container{
    padding:40px;   
    background: linear-gradient(to right, #95dced, #7fcde0, #b7e7ef);
    border-radius:20px ;
}
.cart-items-container h2{
    margin-bottom: 5px
}
.cart-items-container p{
    margin: 0;

}
.items-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}
hr {
    height: 1px;
    background-color: #086076;
    border: none;
    margin: 10px 0;
  }

.prd-img{
    width:125px;
  
}
.prd-img img{
    width:100%;
    border-radius:10%;
}

.cart-total{
    display: flex;
    align-items: center;
    justify-content: right;
}
.sh-cart-icon{
    display: flex;
    align-items: center;
    position: relative;
}
.sh-cart-icon p{
    position: absolute;
    top: -7px;
    width: 20px;
    background: cadetblue;
    border-radius: 50%;
    left: 9px;
    text-align: center;
    color: #fff;

}
button {
    width:100px;
    border-radius: 10%;
    background-color: cadetblue;
    color: rgb(255, 255, 255);
    padding: 10px; 
    margin-right: 10px;
    margin-left: 10px;
    border: none;
    cursor: pointer;
}
button:hover{
    background-color: rgb(16, 143, 148);
}

.clear-cart{
    background-color: brown;
   
}
.clear-cart:hover{
    background-color: rgb(81, 9, 9);
}
.add-sub input{
    text-align: center;
    width: 30px;
    outline: none;  
    padding: 5px;
    font-weight: 800;
    margin: 0 5px 0 5px;
    border: 2px solid #136162;
    border-radius: 5px;
}

.title{
    max-width: 200px;   
    width: 170px;
    text-transform: capitalize;
}

@media (max-width: 767px) {
    /* Adjust font size for mobile devices */
   

.items-info {
    display: flex;    
    flex-direction: column;
    text-align: center;
}
.cart-items-container p{
    margin-bottom: 10px;
    margin-top: 5px;
  

}
.cart-items-container h2 {   
    margin-top: 5px;
  
}
.prd-img {
    width: 100%;
}
.price h3{
     margin: 5px 0 5px 0;
}
  }